function gallery() {
    var swiper = new Swiper(".gallery-collection .mySwiper", {
        spaceBetween: 0,
        slidesPerView: "auto",
        freeMode: true,
        watchSlidesProgress: true,
    });
    var swiper2 = new Swiper(".gallery-collection .mySwiper2", {
        spaceBetween: 0,
        slidesPerView: "auto",
        thumbs: {
            swiper: swiper,
        },
    });
}

function datingSlider() {
    var swiper = new Swiper('.dating-slider .swiper-container', {
        effect: "cards",
        cardsEffect: {
            perSlideOffset: 150,
            perSlideRotate: 15,
            rotate: true,
            slideShadows: false,
        },
        navigation: {
            nextEl: '.dating-slider .swiper-button-next',
            prevEl: '.dating-slider .swiper-button-prev',
        },
        breakpoints: {
            320: {
                effect: "flip",
                cardsEffect: {

                }
            },
            576: {
                effect: "cards",
                cardsEffect: {
                    perSlideOffset: 150,
                    perSlideRotate: 15,
                    rotate: true,
                    slideShadows: false,
                },
            }
        }
    })
}

gallery()
datingSlider()
